import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/pages/index.vue'//1
import Promotion from '@/pages/promotion.vue'//1
import Find from '@/pages/find.vue'//1
import Order from '@/pages/order.vue'//1
import My from '@/pages/my.vue'//1
import Details from '@/pages/details.vue'//1
import Recharge from '@/pages/recharge.vue'//1
import UpgradeList from '@/pages/upgradeList'//
import Upgrade from '@/pages/upgrade.vue'//
import UpgradeEnvelope from '@/pages/upgradeEnvelope.vue'//
import UpgradeRule from '@/pages/upgradeRule.vue'//
import Wallet from '@/pages/wallet.vue' // 1
import Auction from '@/pages/auction.vue'//1
import Login from '@/pages/login.vue'//1
import Register from '@/pages/register.vue'//1
import Forget from '@/pages/forget.vue' //1
import Live from '@/pages/live.vue' //1
import FindDetails from '@/pages/FindDetails.vue' //1
import Jifendetails from '@/pages/jifendetails.vue' //1
import Pay from '@/pages/pay.vue' //
import Address from '@/pages/address.vue' //
import NewAddress from '@/pages/newAddress.vue' //
import Class from '@/pages/class.vue' //
import ClassAuction from '@/pages/classAuction.vue'//1
import Caidetails from '@/pages/caidetails.vue'//

Vue.use(Router);

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'index',
      component: Index
    },
    {
      path: '/index',
      name: 'index',
      component: Index
    },
    {
      path:'/promotion',
      name:'promotion',
      component:Promotion
    },
    {
      path:'/find',
      name:'find',
      component:Find
    },

    {
      path:'/order',
      name:'order',
      component:Order
    },
    {
      path:'/my',
      name:'my',
      component:My
    },
    {
      path:'/login',
      name:'login',
      component:Login
    }
    ,
    {
      path:'/register',
      name:'register',
      component:Register
    },
    {
      path:'/forget',
      name:'forget',
      component:Forget
    },
    {
      path: '/recharge',
      name: 'recharge',
      component: Recharge
    },
    {
      path: '/upgrade-rule',
      name: 'upgrade-rule',
      component: UpgradeRule
    },
    {
      path: '/auction',
      name: 'auction',
      component: Auction
    },
    {
      path: '/wallet',
      name: 'wallet',
      component: Wallet
    },
    {
      path: '/detailsPage',
      name: 'details',
      component: Details
    },
    {
      path: '/upgrade',
      name: 'upgrade',
      component: Upgrade
    },
    {
      path: '/upgradeEnvelope',
      name: 'upgradeEnvelope',
      component: UpgradeEnvelope
    },
    {
      path: '/upgradeList',
      name: 'upgradeList',
      component: UpgradeList
    },
    {
      path: '/live',
      name: 'live',
      component: Live
    },
    {
      path: '/findDetails',
      name: 'findDetails',
      component: FindDetails
    },
    {
      path: '/jifendetails',
      name: 'jifendetails',
      component: Jifendetails
    },
    {
      path: '/pay',
      name: 'pay',
      component: Pay
    },
    {
      path: '/address',
      name: 'Address',
      component: Address
    },
    {
      path: '/newAddress',
      name: 'NewAddress',
      component: NewAddress
    },
    {
      path: '/class',
      name: 'class',
      component: Class
    },
    {
      path: '/classAuction',
      name: 'classAuction',
      component: ClassAuction
    },
    {
      path: '/caidetails',
      name: 'caidetails',
      component: Caidetails
    }
  ]
})



