<template>
    <div class="upgrade-rule">
      <div class="rule-header">
          <div class="rule-title"></div>
      </div>
      <div class="rule-list">
        <p v-for="(item,index) in ruleList">{{item.rule}}</p>
      </div>
    </div>
</template>

<script>
    export default {
        name: 'upgrade-rule',
      data(){
          return {
            ruleList:[
              {rule:"1"},
              {rule:"21000-2001000-22002200-200"},
              {rule:"3"},
              {rule:"4"},
              {rule:"572"}
            ]
          }
      }
    }
</script>

<style lang="less" scoped>
.upgrade-rule{
min-height:100vh;background:#fff;
  .rule-header{width:750px;height:270px;background:url(../assets/images/header-bg.jpg) no-repeat;background-size:100%;overflow:hidden;
    .rule-title{width:320px;height:102px;margin:85px auto 0 auto;line-height:102px;font-family:'';text-align:center;font-size:60px;color:#b16b2c;background:url(../assets/images/large-lace.png) no-repeat;background-size:cover;}
  }
  .rule-list{padding:0 50px;line-height:50px;font-size:28px;
    p{margin-bottom:28px;}
  }


}
</style>



